      collectiontype: "",
      collectiontypeList: [],
      collection_money: "",
      collction_date: "",
      paydate: "",
      plan_man: "",
      plan_manList: [],
      plan_date: "",
      remark: "",
      tableData: [
        {
          collection_number: "1",
          collection_topics: "1",
          collectiontype: "1",
          collection_money: "1",
        },
        {
          collection_number: "1",
          collection_topics: "1",
          collectiontype: "1",
          collection_money: "1",
        },
        {
          collection_number: "1",
          collection_topics: "1",
          collectiontype: "1",
          collection_money: "1",
        },
      ],
      tableDatatwo: [
        {
          pro_abbreviation: "1",
          pro_name: "1",
          model: "1",
          pro_boss: "1",
          Company: "1",
          number: "1",
          order_price: "1",
          order_num: "1",
          stand_price: "1",
          shipping_num: "1",
          no_shipping: "1",
          return_num: "1",
          Collection: "1",
          no_Collection: "1",
          invoice: "1",
          no_invoice: "1",
          pro_busness: "d",
          pro_code: "1111",
          pro_type: "1",
        },
      ],
      dataobj: {},
    });
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    const savecollection = () => {
      proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter: 1,
        },
      });
    };
    const changeDisplay = () => {
      proxy.isDisplay = !proxy.isDisplay;
    };
    return {
      setshow,
      setedit,
      ...toRefs(state),
      savecollection,
      changeDisplay
    };
  },
};
</script>

